header{position:fixed;z-index:999;display:flex;align-items:center;justify-content:center;top:-1px;left:0;width:100%;height:var(--header);color:var(--background-color);background:var(--foreground-color);transition:translate .4s var(--ease);transform:translateY(-0.1%);font-size:14px;font-weight:500;line-height:1.5em;letter-spacing:-0.01em}header div{display:flex;align-items:center;justify-content:space-between;width:100%;height:100%;max-width:var(--body);padding:0 var(--margin);margin:0 auto}header div span:first-child{width:1.25rem;height:1.25rem;background:var(--highlight-color);border-radius:50%}header div span:first-child a{width:100%;height:100%}header.-content{color:var(--foreground-color);background:var(--background-color)}header.-footer{color:var(--background-color);background:var(--foreground-color)}